@mixin btn_hover {
    &:hover {
        background-image: none;
        background-color: $main-color; } }
$main-color: #1eacc7;
$second-color: #ff5656;

header {
  position: relative; }

nav {
  height: 50px;
  background-color: #212121; }

.menu {
  display: flex;
  justify-content: space-between;
  padding: 13px 70px;
  margin: 0;
  list-style-type: none;
  .menu_link {
    color: #ffffff;
    font-size: 15px;
    font-weight: 300;
    &:hover {
      text-decoration: none;
      color: #1eacc7; } }
  .menu_item {
    border-right: 2px dotted black;
    padding-right: 28px;
    &:last-child {
      border-right: none; } } }

.subheader {
  position: absolute;
  width: 100%;
  height: 84px;
  background-color: rgba(0, 0, 0, 0.6);
  &_logo {
    display: block;
    max-width: 100%;
    margin-top: 16px; }
  &_official {
    margin-top: 6px;
    opacity: 0.5;
    color: #ffffff;
    font-size: 13px;
    font-weight: 400; }
  .subheader_call {
    color: #ffffff;
    font-size: 12px;
    font-weight: 300;
    margin-top: 19px;
    text-align: right; }
  .subheader_phone {
    display: block;
    margin-top: 8px;
    color: #ffffff;
    font-size: 22px;
    font-weight: 400;
    text-transform: uppercase;
    text-align: right;
    line-height: 12px; }
  .subheader_btn {
    margin-top: 19px;
    width: 172px;
    height: 46px;
    box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);
    border: none;
    border-radius: 4px;
    background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
    text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
    color: #ffffff;
    font-size: 14px;
    font-weight: 400;
    text-transform: uppercase;
    &:hover {
        background-image: none;
        background-color: $main-color; } } }

.promo {
  min-height: 799px;
  background: url(/img/1st_block_bg.jpg) center (center / cover) no-repeat;
  padding: 150px 0 135px 0;
  .promo_header {
    color: #ffffff;
    font-size: 60px;
    font-weight: 700;
    text-transform: uppercase;
    text-align: center;
    line-height: 60px;
    margin: 0; }
  .promo_subheader {
    color: #ffffff;
    font-size: 36px;
    font-weight: 700;
    text-transform: uppercase;
    text-align: center;
    line-height: 36px;
    margin: 0;
    margin-top: 22px; }
  .promo_descr {
    color: #ffffff;
    font-size: 18px;
    font-weight: 400;
    line-height: 24px;
    text-align: center;
    margin-top: 23px; }
  .promo_btn {
    display: block;
    margin: 118px auto 0 auto;
    width: 259px;
    height: 63px;
    box-shadow: inset 0 -4px 0 rgba(0, 0, 0, 0.12);
    border: none;
    border-radius: 4px;
    border: 3px solid rgba(0, 0, 0, 0.07);
    background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
    text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
    color: #ffffff;
    font-size: 18px;
    font-weight: 400;
    text-transform: uppercase;
    text-align: center;
    @include btn_hover; } }

.opp {
  padding: 71px 400px 65px 400px; }

.subtitle {
  margin: 0 auto;
  width: 112px;
  height: 24px;
  padding: 5px 12px;
  background-color: #000000;
  color: #ffffff;
  font-size: 11px;
  font-weight: 400;
  text-transform: uppercase; }

.title {
  margin-top: 38px;
  color: #222222;
  font-size: 38px;
  margin-bottom: 0;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center; }

.label {
    color: #222222;
    font-size: 17px;
    font-weight: 400;
    line-height: 21px;
    text-align: center;
    margin-bottom: 32px; }

.opp .opp_item {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 58px;
  .opp_round {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 26px;
    width: 113px;
    height: 113px;
    background-color: #1eacc7;
    border-radius: 100%; }
  .opp_item_container {
    width: 343px; }
  .opp_title {
    color: #1eacc7;
    font-size: 17px;
    font-weight: 400;
    line-height: 21px; }
  .opp_descr {
    margin-top: 13px;
    color: #202020;
    font-size: 15px;
    font-weight: 300;
    line-height: 21px; } }

.mobile {
  min-height: 864px;
  padding: 48px 0 79px 0;
  background: url(/img/opportunities_bg.png) center (center / cover) no-repeat;
  &_item {
    position: relative;
    width: 320px;
    height: 280px;
    margin: 0 auto;
    margin-bottom: 19px;
    padding-top: 92px;
    &_1 {
        background: url(/icons/opportunities/1left_one_click.png) center (center / cover) no-repeat; }
    &_2 {
        background: url(/icons/opportunities/1center_reliability.png) center (center / cover) no-repeat; }
    &_3 {
        background: url(/icons/opportunities/1right_prices.png) center (center / cover) no-repeat; }
    &_4 {
        background: url(/icons/opportunities/2left_card_pay.png) center (center / cover) no-repeat; }
    &_5 {
        background: url(/icons/opportunities/2center_feedback.png) center (center / cover) no-repeat; }
    &_6 {
        background: url(/icons/opportunities/2right_division.png) center (center / cover) no-repeat; }
    &_subtitle {
        color: #ffffff;
        font-size: 24px;
        font-weight: 400;
        line-height: 31px;
        text-transform: uppercase;
        text-align: center; }
    &_plus {
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        bottom: 35px;
        left: 50%;
        transform: translateX(-50%);
        width: 26px;
        height: 26px;
        background-color: #000000;
        margin: 0 auto;
        border-radius: 100%;
        cursor: pointer;
        &:before {
            content: '';
            display: block;
            width: 14px;
            height: 2px;
            background-color: $main-color;
            transition: 0.3s all;
            transform: translateY(-45%); }
        &:after {
            content: '';
            position: absolute;
            left: 50%;
            transform: translateX(-45%);
            display: block;
            height: 14px;
            width: 2px;
            background-color: $main-color;
            transition: 0.3s all; }
        &:hover {
            &:before, &:after {
                background-color: $second-color; } } } } }


.car {
    padding: 57px 0 138px 0;
    height: 499px;
    background-color: #070716;
    .subtitle {
        margin: 0 auto;
        width: 112px;
        height: 24px;
        padding: 5px 12px;
        background-color: #ffffff;
        color: #070716;
        font-size: 11px;
        font-weight: 400;
        text-transform: uppercase; }
    .title {
        margin-top: 38px;
        color: #ffffff;
        font-size: 38px;
        margin-bottom: 0;
        font-weight: 700;
        text-transform: uppercase;
        text-align: center; }
    .label {
        color: #ffffff;
        font-size: 17px;
        font-weight: 400;
        line-height: 21px;
        text-align: center;
        margin-bottom: 32px; } }

.car_bottom {
    position: relative;
    height: 397px;
    background: url(/img/opportunities_bg.png) center (center / cover) no-repeat;
    img {
        position: absolute;
        bottom: 184px;
        left: 50%;
        transform: translateX(-50%);
        transform: translateZ(40%); }
    .car_text {
        position: absolute;
        bottom: 138px;
        left: 50%;
        transform: translateX(-50%);
        color: #222222;
        font-size: 17px;
        font-weight: 300;
        text-align: center; } }

.require {
    padding: 68px 0 84px 0;
    &_block {
        margin: 33px 0;
        &_nmb {
            margin-bottom: 0px; } }
    &_title {
        color: #1ca8c3;
        font-size: 22px;
        font-weight: 400; }
    &_list {
        list-style-type: none;
        padding-left: 50px;
        margin-top: 22px;
        li {
            position: relative;
            font-size: 17px;
            font-weight: 300;
            line-height: 24px;
            margin-bottom: 10px;
            &:before {
                content: '';
                display: block;
                position: absolute;
                left: -32px;
                top: 3px;
                width: 21px;
                height: 20px;
                background: url(/icons/blue_check_mark.png) center (center / cover) no-repeat; } } }
    &_descr {
        margin-top: 25px;
        font-size: 17px;
        font-weight: 300;
        line-height: 24px;
        color: #1ca8c3; }
    .warning {
        .require_title {
            color: $second-color; }
        .require_list {
            li {
                &:before {
                    background: url(/icons/red_check_mark.png); } } } } }

.map {
    padding: 49px 0;
    min-height: 717px;
    background: url(/img/towns_map_bg.jpg) center (center / cover) no-repeat;
    position: relative;
    &_subtitle {
        margin: 0 auto;
        width: 112px;
        height: 24px;
        padding: 5px 12px;
        background-color: #ffffff;
        color: #070716;
        font-size: 11px;
        font-weight: 400;
        text-transform: uppercase;
        text-align: center; }
    &_title {
        margin-top: 38px;
        color: #ffffff;
        font-size: 38px;
        margin-bottom: 0;
        font-weight: 700;
        text-transform: uppercase;
        text-align: center; }
    &_label {
        color: #ffffff;
        font-size: 17px;
        font-weight: 400;
        line-height: 21px;
        text-align: center;
        margin-bottom: 32px; }
    &_descr {
        position: absolute;
        left: 45%;
        bottom: 49px;
        text-align: center;
        font-size: 15px;
        text-transform: uppercase;
        font-weight: 400;
        color: $second-color;
        &:hover {
            text-decoration: none;
            color: #fff; } } }


footer {
  padding: 14px 23px 39px 23px;
  a {
    &:hover {
      text-decoration: none; } } }
.footer {
  &_divider {
    width: 299px;
    height: 4px;
    background-color: $main-color;
    margin: 0 auto; }
  &_wrapper {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    padding-top: 32px; }
  &_wrapper > div {
    width: 33%;
    &:nth-child(3) {
      display: flex;
      justify-content: flex-end; } }
  &_social {
    width: 160px;
    display: flex;
    justify-content: space-between;
    img {
      width: 32px;
      height: 32px; } }
  &_links {
    width: 390px;
    margin: 0 auto;
    .first_row {
      display: flex;
      justify-content: space-between;
      padding: 0 10px;
      a {
        color: #070716;
        font-size: 15px;
        font-weight: 300;
        text-transform: uppercase; } }
    .second_row {
      display: flex;
      justify-content: space-between;
      margin-top: 10px;
      a {
        color: #999999;
        font-size: 11px;
        font-weight: 300;
        text-transform: uppercase; } }
    .lang {
      display: flex;
      justify-content: center;
      color: #1fbad6;
      font-size: 11px;
      font-weight: 400;
      text-transform: uppercase;
      text-align: center;
      margin-top: 19px; } }
  &_apps {
    width: 296px;
    display: flex;
    justify-content: space-between;
    img {
      width: 97px;
      height: 75px; } } }
